:root{
    --main:#e50914;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;outline: none;
}
body,html{
    overflow-x: hidden;
}
body{
    font-family: sans-serif;  
    background: #000;
    color: #fff;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
/* ====MEDIA QUERIES=== */
@media(max-width:481px){
    .section-heading h1{
        font-size: 2.2rem;
    }
}
@media(max-width:423px){
    .section-heading h1{
        font-size: 2rem;
    }
}
@media(max-width:385px){
    .section-heading h1{
        font-size: 1.8rem;
    }
    .content p{
        font-size: 1.3rem;
        line-height: 1.2;
    }
}
@media(max-width:346px){
    .section-heading h1{
        font-size: 1.6rem;
    }
     .content p{
        font-size: 1.2rem;
        line-height: 1.2;
    }
}