.container{
    border-top: 10px solid #222222;
}
.enjoy{
    display: flex;
    margin:5em auto 3.75em ;
    width: 93%;
    gap: 10%;
    max-width: 1100px;
    justify-content: space-between;
    align-items: center;
}

img{
    width: 100%;
}
.enjoy .left{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.section-heading h1{
    font-size: 3rem;
}
.section-heading{
    margin-bottom: 1.2em;
}
.content p{
    font-size: 1.6rem;
    line-height: 1.4;
}
.content{
    max-width: 550px;
}
.enjoy .tv{
    width: 85%;
}
/* ====MEDIA QUERIES==== */

@media(max-width:950px){
    .enjoy{
        flex-direction: column;
        text-align: center;
    }
    .content{
        max-width: 1700px;
        margin-bottom: 3em;
    }
    .content p{
        font-size: 1.4rem;
        line-height: 1.3;
    }
    .section-heading h1{
        font-size: 2.5rem;
    }

    .enjoy .tv{
        width: 90%;
        margin: 0 auto;
    }
}