.home__page{
    width: 100vw;
    height: 100vh;
    position: relative;
}
.unlimited{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    z-index: 2;

}
.home__page::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.6);
    z-index: 1;
}
a{
    color: #fff;
}
.home__nav {
    position: relative;
    height:90px ;
    width: 93%;
    margin: 0 auto;
    z-index: 2;
}
.logo{
    position: absolute;
    top: 50%;
    left: 25px;
    transform: translate(-50%, -50%);
    color: var(--main);
    font-size: 5rem;
    margin-top: 15px;
}
.sign{
    position: absolute;
    top: 50%;
    right: -30px;
    transform: translate(-50%, -50%);
    color: var(--main);
}
a.btn{
    display: inline-block;
    background-color: var(--main);
    padding: .5em 1em;
    border-radius: 3px;
    text-align: center;
}
.heading h1{
    color: #fff;
    font-size: 3.1rem;
    text-align: center;
}
.heading{
    margin-bottom: 1.1em;
}
p{
    color: #fff;
}
.smaller-heading p{
    font-size: 1.7rem;
}
.smaller-heading{
    margin-bottom:1.7em ;
}
.smallest-heading p{
    font-size: 1.1rem;
}
.smallest-heading{
    margin-bottom: 1.3em;
}
.get-started{
    display: grid;
    grid-template-columns: 70% 30%;
}
.email input{
    width: 100%;
    height: 100%;
    font-size: 1rem;
    padding: 1em 0 1em .5em;
}
.get-started a.btn{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    height: 100%;
}
/* ====MEDIA QUERIES==== */
