.download{
    display: flex;
    gap: 17%;
    margin:0.625em auto 3.125em ;
    width: 93%;
    max-width: 1100px;
}
.download .left img{
    position: relative;
}
.eleven{
    width: 70%;
    margin: 3.125em auto 0;
}
.boxshot{
    position: absolute;
    bottom: 20px;
    width: 100%;
    background-color: #000000;
    border: 2px solid #3d3d3d;
    border-radius: 12px;
    padding: .5em;
}
.right .section-heading{
    max-width: 540px;
}
.right .section-heading h1{
    margin-top: 1.5em;
}
.boxshot-content{
    display: flex;
    align-items: center;
    gap: 15px;
}
.boxshot-image{
    width: 50px;
}
p.loading{
    color: #0071eb;
}
.download .left{
    position: relative;
}
/* ====MEDIA QUERIES==== */
@media(max-width:1023px){
    .download .section-heading{
        max-width: 600px;
    }
}
@media(max-width:993px){
    .download .section-heading{
        max-width: 650px;
    }
}
@media(max-width:967px){
    .download .section-heading{
        max-width: 700px;
    }
}

@media(max-width:950px){
    .download{
        flex-direction: column-reverse;
        align-items: center;
    }
    .download .right{
        text-align: center;
    }
    .right .section-heading{
        max-width: 1700px;
    }
    .download .left {
        width: 45%;
    }
    .boxshot{
        bottom: 30px;
    }
}
@media(max-width:702px){
    .download .left {
        width: 50%;
    }
}
@media(max-width:631px){
    .download .left {
        width: 55%;
    }
}
@media(max-width:574px){
    .download .left {
        width: 60%;
    }
}
@media(max-width:527px){
    .download .left {
        width: 65%;
    }
}
@media(max-width:486px){
    .download .left {
        width: 70%;
    }
}
@media(max-width:451px){
    .download .left {
        width: 75%;
    }
}
@media(max-width:421px){
    .download .left {
        width: 80%;
 
    }
}
@media(max-width:364px){
    .boxshot{
        bottom: 0;
    }
    .download .left {
        width: 85%;
 
    }
}

