.kids{
    width: 93%;
    display: flex;
    align-items: center;
    margin: 5em auto;
    max-width: 1100px;
}
.kid-img{
    width: 80%;
}
.kids .content{
    max-width: 500px;
}
.kids .section-heading h1{
    font-size: 2.95rem;
}
.kids .right{
    align-self: flex-end;
}
/* =======MEDIA QUERIES=== */
@media(max-width:950px){
    .kids{
        flex-direction: column-reverse;
    }
    .kids .right{
        width: 100%;
        text-align: center;
    }
    .kids .content{
        max-width: 1700px;
    }
    .kid-img{
        margin: 0 auto;
    }
    .kids .section-heading h1{
        font-size: 2.5rem;
    }
}
@media(max-width:481px){
    .kids .section-heading h1{
        font-size: 2.2rem;
    }
}
@media(max-width:423px){
    .kids .section-heading h1{
        font-size: 2rem;
    }
}
@media(max-width:385px){
    .kids .section-heading h1{
        font-size: 1.8rem;
    }
    .kids .content p{
        font-size: 1.3rem;
        line-height: 1.2;
    }
}
@media(max-width:346px){
    .kids .section-heading h1{
        font-size: 1.6rem;
    }
    .kids .content p{
        font-size: 1.2rem;
        line-height: 1.2;
    }
}