.frequently{
    width: 93%;
    margin: 3.125em auto 3.75em;
    max-width: 800px;
}
.title h1{
    font-size: 3.1rem;
}
.title{
    margin: 0 0em 3em;
    text-align: center;
}

li.question{
    background-color: #303030;
    margin-bottom: .5em;
    position: relative;
}
li.question a{
    font-size: 1.6rem;
    display:inline-block;
    padding: 1.5rem 1.7rem 1.6rem 1.7rem;
}
a.quest{
    width: 400%;
}
.down-part{
    width: 80%;
    margin: 4em auto 0;
}
.frequently .smallest-heading{
    text-align: center;
}
.smallest p{
    font-size: 1.1rem;
}
.plus{
    position: absolute;
    right: 40px;
    font-size: 2.3rem;
}

/* ======MEDIA QUERIES==== */
@media(max-width:950px){
    .title h1{
        font-size: 2.5rem;
    }
    li.question a{
        font-size: 1.4rem;
    }
    .frequently{
        max-width: 700px;
    }
}
@media(max-width:481px){
    .title h1{
        font-size: 2.2rem;
    }
    .plus{
        font-size: 2.2rem;
    }
    li.question a{
        font-size: 1.2rem;
    }
}
@media(max-width:470px){
    .plus{
        right: 10px;
        font-size: 2.3rem;
    }
}
@media(max-width:423px){
    .title h1{
        font-size: 2rem;
    }
    .plus{
        font-size: 2rem;
    }
    li.question a{
        font-size: 1em;
    }
}
@media(max-width:408px){
    .frequently{
        width: 100%;
    }
}
@media(max-width:385px){
    .title h1{
        font-size: 1.8rem;
    }
    li.question a{
        font-size: 1rem;
    }
    .plus{
        font-size: 1.8rem;
    }
}
@media(max-width:346px){
    .title h1{
        font-size: 1.6rem;
    }
    li.question a{
        font-size: 1rem;
    }
    .plus{
        font-size: 1.6rem;
    }
}
@media(max-width:296px){
    li.question a{
        padding: 1.5rem 1rem 1.6rem 1rem;
        font-size: .9rem;
    }
}

