.footer{
    width: 93%;
    margin: 5em auto 1em;
    max-width: 950px;
}
.footer a{
    color:#737373;
    transition: 0.4s;
}
.footer a:hover{
    text-decoration: underline;
}
.question{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    margin-bottom: 2em;
}
.question a{
    font-size: .8rem;
}
.nigeria a{
    font-size: .9rem;
}
.questions {
    text-align: left;
    align-self: flex-start;
    margin-bottom: 2em;
}
.questions p{
    font-size: 1.1rem;
}
/* ===MEDIA QUERIES=== */
@media(max-width:1100px){
    .footer{
        max-width: 700px;
    }
}
@media(max-width:836px){
    .footer{
        margin-left: 5em;
    }
}
@media(max-width:741px){
    .question{
        grid-template-columns: 1fr 1fr 1fr ;
    }
}
@media(max-width:501px){
    .question{
        grid-template-columns: 1fr 1fr;
    }
    .footer{
        margin-left: 3em;
    }
}